.voyage {
  padding: 123px 0 117px;
  color: #011C40;
  background-color: #F9FBFD;
}
.voyage__container {
  position: relative;
  display: table;
  height: 301px;
}
.voyage__container::after {
  position: absolute;
  content: "";
  width: 41%;
  max-width: 560px;
  height: 100%;
  top: 50%;
  transform: translateY(-50%);
  right: 7.54%;
  background-image: url("../img/penguins_desktop.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 560px 301px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .voyage__container::after {
    background-image: url("../img/penguins_desktop@2x.jpg");
  }
}
.voyage__wrapper {
  display: table-cell;
  vertical-align: middle;
}
.voyage__wrapper h2 {
  margin: 0;
  padding: 0;
  width: 38%;
  margin-bottom: 25px;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
.voyage__wrapper p {
  width: 46%;
  margin: 0;
  padding: 0;
  padding-bottom: 8px;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .voyage {
    padding: 89px 0 93px;
  }
  .voyage__container {
    height: 264px;
  }
  .voyage__container::after {
    width: 42.2%;
    height: 264px;
    right: 45px;
    background-image: url("../img/penguins_tablet.jpg");
    background-size: 324px 264px;
  }
  .voyage__wrapper h2 {
    width: 38%;
    margin-bottom: 18px;
    font-size: 26px;
    line-height: 36px;
  }
  .voyage__wrapper p {
    width: 50%;
    padding: 0;
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .voyage__container::after {
    background-image: url("../img/penguins_tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .voyage {
    flex-direction: column;
    padding: 57px 0 65px;
  }
  .voyage__container {
    min-height: auto;
    padding-bottom: 220px;
  }
  .voyage__container::after {
    width: 100%;
    height: 186px;
    bottom: 0;
    top: auto;
    left: 50%;
    transform: translateX(-50%);
    background-image: url("../img/penguins_mobile.jpg");
    background-position: center bottom;
    background-size: 290px 186px;
  }
  .voyage__wrapper h2 {
    width: 70%;
    margin-bottom: 15px;
    font-size: 24px;
    line-height: 34px;
  }
